<template>
  <!-- Project List Panel -->
  <div class = "editor_panel main_shadow">
    <div class="left">
      <div class = "project_list" v-for="p in state.projects">
        <h1> {{p.name}} </h1>
        <ul>
          <li v-for = "f in p.files" v-bind:class="{'selected':selected == $index && pselected == $parent.$index}" v-on:click = "choose_folder($parent.$index, $index, $event)" id = "{{f.name}}"> {{f.name}} </li>
        </ul>
      </div>

    </div>
    <div class="right">
      <div class = "project_info">
        <p>Double click to open</p>
        <div class="img-cover"></div>
        <div class = "actions">
          <div class="action-element"></div>
          <div class="action-element"></div>
          <div class="action-element"></div>
        </div>
      </div>
      <ul>
        <li v-for = "f in folder_chosen" v-on:click = "load_project(f)"> {{f.name}} </li>
      </ul>
    </div>
   </div>
</template>

<script>
import Store from '../Store'

export default {
  name: 'ProjectLoad',
  data () {
    return {
      state: Store.state,
      id: Store.state.id,
      pselected: -1,
      selected: -1,
      folder_chosen: [],
      uri: {
        folder: '',
        fullpath: ''
      }
    }
  },
  methods: {
    new_project: function () {
      return true
    },
    choose_folder: function (pindex, index, event) {
      // console.log(pindex, index, this.projects[pindex].files[index].name)
      this.pselected = pindex
      this.selected = index
      this.uri.folder = '/' + this.state.projects[pindex].name + '/' + this.state.projects[pindex].files[index].name
      this.folder_chosen = this.state.projects[pindex].files[index].files
    },
    load_project: function (folder) {
      this.uri.fullpath = this.uri.folder + '/' + folder.name
      // Store.emit('project_action', '/run', this.uri.fullpath)
      Store.emit('project_load', this.uri.fullpath)
      Store.emit('toggle', 'load_project')
    }
  },
  created () {
    Store.on('new_project', this.new_project)
  },
  destroyed () {
    Store.remove_listener('new_project', this.new_project)
  }
}
</script>

<style lang='less'>
@import "../assets/css/variables.less";

.editor_panel {
  .project_list {
    margin-bottom: 12px;
    font-family: 'Open Sans';

    h1 {
      color: @primaryAccent;
      font-weight: 600;
      padding: 5px 0px;;
      border-bottom: 0px solid @primaryAccent;
      margin-bottom: 5px;
      text-transform: uppercase;
    }

  }

  ul {
    text-align: left;
    list-style: none;
    margin: 0;
    padding: 0;
    cursor: pointer;

    li {
      padding: 5px;
      font-size: 1.3em;
      font-weight: 300;

      &:hover, &.selected {
        background-color: rgba(255, 255, 255, 0.69);
        border-radius: 1px;
        color: black;
      }

      &:active {
        background: rgba(0, 0, 0, 0.1);
      }
    }
  }

  .right {
  	text-align: center;

    .project_info {
      display: none;
    }

    .img-cover {
      width: 100px;
      height: 100px;
      background-color: blue;
    }

    .actions {
    	margin-top: 10px;

    	.action-element {
    		width: 20px;
  		  height: 20px;
  		  background-color: green;
  		  display: inline-block;
    	}
    }

    ul {
      padding: 0.5em;
      font-weight: 500;
    }
  }

}

</style>



